.m {
  width: 1000px;
  margin: 0 auto;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.left-wrap {
  width: 70%;
}
.right-wrap {
  width: 28%;
}
/* 三行文本 */
.clamp-line-3 {
  -webkit-line-clamp: 3;
  overflow: hidden;
  display: -webkit-box;
  display: -moz-box;
  display: box;
}
/*单行文本*/
.single-line {
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.article-list li {
  background-color: #fff;
  padding: 15px;
  margin-bottom: 10px;
}
.article-list li .pic {
  width: 165px;
  overflow: hidden;
  margin-right: 20px;
  border-radius: 5px;
  float: left;
  display: block;
  height: 110px;
}
.article-list li .pic img {
  width: 100%;
}
.article-list li h3 {
  font-size: 16px;
  font-weight: bold;
  color: #000;
  line-height: 30px;
  margin-bottom: 8px;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.article-list li .desc {
  font-size: 12px;
  color: #666;
  line-height: 24px;
  -webkit-line-clamp: 3;
  overflow: hidden;
  display: -webkit-box;
  display: -moz-box;
  display: box;
}
.article-list li .statistics {
  width: 100%;
  font-size: 12px;
  line-height: 20px;
  margin-top: 10px;
  color: #999;
}
.article-list li .statistics span {
  margin-right: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  float: left;
}
.article-list li .statistics .iconfont {
  margin-right: 5px;
  font-size: 14px;
}
.article-list li .statistics .icon-tag {
  color: #f65a8a;
  vertical-align: middle;
}
.article-list li .statistics .icon-time {
  color: #1E9FFF;
}
.article-list li .statistics .icon-pulse {
  color: #f00;
}
.article-list li .statistics a {
  color: #096;
}
.home .banner {
  height: 400px;
  width: 100%;
  background-color: #393D49;
  margin-bottom: 20px;
}



