.m {
  width: 1000px;
  margin: 0 auto;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.left-wrap {
  width: 70%;
}
.right-wrap {
  width: 28%;
}
/* 三行文本 */
.clamp-line-3 {
  -webkit-line-clamp: 3;
  overflow: hidden;
  display: -webkit-box;
  display: -moz-box;
  display: box;
}
/*单行文本*/
.single-line {
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.header {
  height: 66px;
  background-color: #fff;
  border-bottom: 1px solid #e5e5e5;
  line-height: 66px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.header .logo {
  color: #000;
  font-size: 30px;
  font-weight: bold;
}
.header ul li {
  float: left;
  font-size: 14px;
  margin-left: 20px;
}
.header ul li a {
  position: relative;
  display: inline-block;
  color: #000;
  padding: 0 10px;
}
.header ul li a.active {
  color: #f65a8a;
}



